.modal {
  display: none;
  height: 100vh;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
  .modal__bg {
    background: rgba(255, 255, 255, 0.5);
    height: 100vh;
    position: absolute;
    width: 100%;
  }
  .modal__content {
    display: block;
    position: absolute;
    top: 41%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 550px;
    min-height: 600px;
    background-color: #ffffff;
    @include mq("lpc") {
      top: 45%;
      width: 400px;
    }
    @include mq("tab") {
      width: 350px;
    }
    @include mq("sp") {
      top: 55%;
      width: 280px;
      overflow: scroll;
    }
    .cross {
      margin-left: auto;
      top: 20px;
      right: 22px;
      display: block;
      width: 18px;
      height: 18px;
      position: relative;
      cursor: pointer;
      &::before,
      &::after {
        position: absolute;
        content: " ";
        top: 50%;
        left: 50%;
        width: 2px;
        height: 25px;
        background-color: #707070;
      }

      &::before {
        transform: translate(-50%, -50%) rotate(45deg);
      }

      &::after {
        transform: translate(-50%, -50%) rotate(-45deg);
      }
    }
    .modal__content__inner {
      padding: 12px 60px 90px 60px;
      .modal__content__ttl {
        text-align: center;
        font-size: rem(20);
        padding-bottom: 29px;
        border-bottom: 1px solid #e7e7e7;
        margin-bottom: 35px;
      }
      form {
        .box {
          margin-top: 14px;
          width: 100%;
          label {
            font-size: rem(16);
          }
          input {
            margin-top: 14px;
            width: 100%;
            height: 32px;
            border: 1px solid #e7e7e7;
            &::placeholder {
              color: #797878;
            }
          }
          select {
            margin-top: 14px;
            width: 100%;
            height: 36px;
            border: 1px solid #e7e7e7;
            &:invalid {
              color: #797878;
            }
          }
        }
        .submit-btn {
          margin-right: auto;
          margin-left: auto;
          margin-top: 64px;
          display: block;
          text-align: center;
          width: 198px;
          max-width: 198px;
          height: 42px;
          border: 1px solid #707070;
          font-size: rem(16);
          line-height: 42px;
          @include mq('sp') {
            width: 130px;
          }
          &:hover {
            opacity: 0.5;
            transition: $transition;
          }
        }
      }
    }
  }
}

// flatpickrの体裁
@import "flatpickr/dist/flatpickr.min.css";
$red: #f00;
$blue: #25bdcf;
// 日曜日：赤
.flatpickr-calendar
  .flatpickr-innerContainer
  .flatpickr-weekdays
  .flatpickr-weekday:nth-child(7n + 1),
.flatpickr-calendar
  .flatpickr-innerContainer
  .flatpickr-days
  .flatpickr-day:not(.flatpickr-disabled):not(.prevMonthDay):not(.nextMonthDay):nth-child(7n
    + 1) {
  color: $red;
}
// 土曜日：青
.flatpickr-calendar
  .flatpickr-innerContainer
  .flatpickr-weekdays
  .flatpickr-weekday:nth-child(7),
.flatpickr-calendar
  .flatpickr-innerContainer
  .flatpickr-days
  .flatpickr-day:not(.flatpickr-disabled):not(.prevMonthDay):not(.nextMonthDay):nth-child(7n) {
  color: $blue;
}
// 祝日
.flatpickr-day.is-holiday {
  background: lighten($red, 40%) !important;
}
// 入力欄の文字列を選択させないようにしておく
.flatpickr-calendar .numInput {
  user-select: none;
}
.flatpickr-wrapper {
  display: block;
  height: 36px;
}
.datepickerStyle {
  height: 100%;
}
